.footer-content {
  background-color: #003d4e;
  box-shadow: inset 0 8px 20px -6px #000;
}

.footer-text {
  color: #ff00001a;
  text-transform: uppercase;
  background-image: url("animating-text.3fad5e72.jpg");
  background-position: center;
  background-size: cover;
  background-clip: text;
  font-size: 20px;
  font-weight: 700;
  animation: 25s infinite background-text-animation;
}

@keyframes background-text-animation {
  0% {
    background-position: 0;
  }

  50% {
    background-position: 1500px;
  }

  100% {
    background-position: 0;
  }
}
